	module.exports = {"data":{"markdownRemark":{"html":"<p>Educational Testing Services — they're the folks that make the SAT and GRE — has a new test for ICT literacy. Despite a humorously useless Flash intro, the test appears to be fully baked. The sample questions on the ETS website look at the ways a test-taker would represent and evaluate information in an online context. </p>\n<p>Test taking is boring. It's a boring subject. But you can't direct change in any environment without having some feedback. So this investment in ICT literacy evaluation is good to see. </p>\n<p>Give them a visit:\n<a title=\"ETS ICT Literacy Assessment Tests Information and Communication Technology Proficiency and Computer Skills\" href=\"http://www.ets.org/ictliteracy/index.html\">ETS ICT Literacy Assessment Tests Information and Communication Technology Proficiency and Computer Skills</a></p>","frontmatter":{"created_at":"August 03, 2005","path":"/2005/08/03/measuring-ict-literacy","title":"Measuring ICT Literacy","tags":["education","measurement","evaluation","literacy"]}}},"pathContext":{}}

